@import '../../app/app';

.playTime {
  padding: 40px 0 60px;

  .itemWrap {
    display: flex;
    justify-content: space-between;
    width: 960px;
    margin: 50px 0 120px;
  }
}

@include  TabletMobile {
  .playTime {
    width: 95%;
    padding: 10% 2%;
    .itemWrap{
      margin-top: 0;
      width: 100%;
      justify-content: center;
      flex-direction: column;
      align-items: center;
    }
  }
}