@import '../../app/app';

.BarWrap {
  margin-top: 85px;
  width: 100%;

  .PlayTimeTop {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;

    h3 {
      font-size: 22px;
      font-weight: 500;
      line-height: 32px;
      color: #fff;
      cursor: default;

      &:hover ~ .ReFreshNotice {
        display: flex;
      }

      span {
        font-weight: 900;
        color: #4D4FDB;
      }
    }

    .reload {

      margin-bottom: 8px;

      a {
        display: flex;
        align-items: center;
        line-height: 32px;
        cursor: pointer;
        font-size: 16px;
        color: #fff;
        font-weight: 400;

        img {
          margin-right: 15px;
          transition: 0.5s;
        }
      }
    }

    .ReFreshNotice {
      position: absolute;
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      top: 30px;
      right: 0;
      width: 100%;
      height: 70px;
      max-width: 460px;
      background: #FEFEFE;
      border: 1px solid #D9D9D9;
      display: none;
      align-items: center;
      justify-content: center;
      z-index: 20;
      transition: all 0.5s;
    }
  }


  .ProgressContainer {
    width: 100%;
    max-width: 880px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid rgba(77, 79, 219, 0.5);
    border-radius: 100px;
    padding: 32px 40px;
    margin: 40px auto 0;


    .defaultBar {
      width: calc(100% - 30px);
      max-width: 100%;
      height: 2px;
      background: rgba(255, 255, 255, 0.2);

      .ProgressBar {
        height: 100%;
        background: #E83767;
        transition: All 0.3s;
        position: relative;
      }

      .PCTime {
        position: absolute;
        top: 25px;
        z-index: 10;

        .PcTimeItem {
          display: block;
          width: 166px;
          height: 50px;
          background-color: #FE4879;
          position: relative;
          color: #fff;
          font-size: 24px;
          font-weight: 700;
          line-height: 50px;
          border-radius: 100px;

          &:before {
            content: "";
            position: absolute;
            top: -10px;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 0;
            height: 0;
            border-bottom: 13px solid #FE4879;
            border-top: 13px solid transparent;
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
            z-index: -1;
          }
        }
      }
    }
  }

  .hours {
    width: calc(100% - 40px);
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 32px 20px;

    li {
      width: 68px;
      height: 40px;
      font-size: 16px;
      font-weight: 400;
      color: #fff;
      line-height: 40px;
      border: 1px solid rgba(255, 255, 255, 0.2);
      border-radius: 100px;
      position: relative;

      span {
        width: calc(100% - 166px);
        height: 100%;
        max-width: 24px;
        max-height: 24px;
        background-image: url("../../assets/images/event1/Subtraction.svg");
        background-repeat: no-repeat;
        background-position: center 50%;
        background-color: #00041D;
        background-size: 24px 24px;
        padding: 5px 20px;
        top: -95px;
        left: 15px;
        position: absolute;
      }

      &:first-child span {
        background-image: url("../../assets/images/event1/ActiveSubtraction.svg");
      }
    }
  }
}

@include TabletMobile {
  .BarWrap {
    width: 100%;
    height: 100%;
    margin-top: 40px;

    .PlayTimeTop {
      h3 {
        margin-bottom: 10px;
      }
    }

    .ProgressContainer {
      width: 100%;
      height: 100%;
      margin: 10px auto 0;
      padding: 25px 0;

      .defaultBar {
        width: calc(100% - 40px);
        max-width: 100%;
        height: 2px;
        background: rgba(255, 255, 255, 0.2);

        .ProgressBar {
          height: 100%;
          background: #E83767;
          transition: All 0.3s;
          position: relative;
        }

        .PCTime {
          top: 18px;
          z-index: 10;

          .PcTimeItem {
            width: 80px;
            height: 36px;
            font-size: 14px;
            line-height: 36px;

            &:before {
              top: -10px;
              left: 50%;
              border-bottom: 15px solid #FE4879;
              border-top: 18px solid transparent;
              border-left: 8px solid transparent;
              border-right: 8px solid transparent;
            }
          }
        }
      }
    }

    .hours {
      width: calc(100% - 10px);
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 25px 0;


      li {
        width: 48px;
        height: 24px;
        font-size: 12px;
        line-height: 24px;

        span {
          width: calc(100% - 80px);
          max-width: 16px;
          max-height: 16px;
          background: url('../../assets/images/event1/Subtraction.svg') no-repeat center 50% #00041D;
          background-size: 16px 16px;
          padding: 5px 20px;
          top: -65px;
          left: 15px;
          position: absolute;
        }
      }
    }
  }
}
