@import '../../app/app';


.modalWrap {
  max-width: 360px;
  width: 100%;
  height: 100%;
  min-height: 145px;
  max-height: 169px;
  padding: 20px 50px;
  background: #FFFFFF;
  border: 1px solid #CCCCCC;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  &:focus {
    border: none;
    outline: none;
  }


  p {
    color: #3A0F85;
    font-size: 18px;
    line-height: 26px;
    font-weight: 400;
    margin-bottom: 20px;

    span {
      color: #3A0F85;
      font-weight: 700;
      display: block;
      font-size: 24px;
      line-height: 35px;
    }
    strong{
      color: #3A0F85;
      font-weight: 700;
    }
  }

  .errorModal {
    font-size: 20px;
  }

  .buttonWrap {
    button {
      font-family: 'Noto Sans KR', sans-serif;
      cursor: pointer;
      width: 170px;
      height: 60px;
      background: #FE4879;
      border-radius: 30px;
      border: none;
      color: #fff;
      font-size: 24px;
      font-weight: 500;
      position: relative;
      line-height: 60px;

      &:hover {
        background: #FE4879;
      }

      &:before {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        background: #ECDEE7;
        border-radius: 30px;
        top: 4px;
        left: 0;
        z-index: -1;
      }

      &:last-child{
        background: #3A0F85;
        margin-left: 20px;

        &:hover {
          background: #3A0F85;
        }

        &:before {
          background: #F4F4F4;
        }
      }



      &:focus {
        border: none;
        outline: none;
      }
    }
  }
}


@include TabletMobile {
  .modalWrap {
    width: 100%;
    max-width: 300px;
    height: 154px;

    p {
      font-size: 16px;
      line-height: 23px;
      margin-bottom: 10px;
    }

    .errorModal {
      font-size: 16px;
      line-height: 23px;
      margin-bottom: 10px;
    }

    .buttonWrap {
      button {
        width: 100px;
        height: 40px;
        font-size: 18px;
      }
    }
  }

}
