/*
Do something for global styles.
    ex1 > import variables.
    ex2 > import mixin.
 */
@import '../styles/common.module';
@import '../styles/mixin.scss';

.app {
  width: 100%;
  height: 100%;
  background: url("../assets/images/bg.jpg") repeat center bottom #00041D;
  background-size: contain ;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  &:before{
    content: "";
    position: absolute;
    width: 50%;
    height:2035.78px;
    max-width: 1920px;
    background: url("../assets/images/Pattern01.svg") no-repeat;
    left: 0;
    top:10%;
    mix-blend-mode: soft-light;
    opacity:20%;
    z-index: 1;
  }

  &:after{
    content: "";
    position: absolute;
    width: 50%;
    height:2035.78px;
    max-width: 1920px;
    background: url("../assets/images/Pattern02.svg") no-repeat;
    right: 0;
    bottom:15%;
    mix-blend-mode: soft-light;
    opacity: 40%;
    z-index: 1;
  }

  .bg_top {
    position: absolute;
    top: 0;
    background: url("../assets/images/bg_top.jpg") no-repeat 50% top ;
    background-size: contain;
    width: 100%;
    height: 100%;
    max-height: 1080px;
    overflow: hidden;
    z-index: 0;

    @media (min-width:1940px) {

      &:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 20%;
        bottom: -5%;
        left: 50%;
        transform: translateX(-50%);
        background: linear-gradient( to top, #00041D 60%, transparent 50%  ) no-repeat;
        filter : blur(30px);
        z-index: 1;
      }

      &:after{
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        min-width: 1940px;
        max-width: 2000px;
        color: transparent;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
        filter : blur(30px);
        background: linear-gradient(to left, #00041D 3%, transparent 6% ,transparent 96% , #00041D 97%  );
        z-index: 1;
      }
    }
  }

  .container {
    display: inline-block;
    width: 100%;
    max-width: $content-size;
    margin: 0 auto 73px;
  }

  main {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    z-index: 2;

  }
}


.PCDisplay {
  display: block;
}

.PCTabletDisplay {
  display: block;
}

.TabletMobileDisplay {
  display: none;
}

.mobileDisplay {
  display: none;
}


@include TabletMobile {
  .PCDisplay {
    display: none;
  }
  .TabletMobileDisplay {
    display: block;
  }
  .app {

    &:before{
      width: 70%;
      height:70%;
      background-size: contain;
      top:30%;
    }

    &:after{
     content: unset;
    }

    .bg_top {
      background: url("../assets/images/bg_top.jpg") no-repeat 55% top ;
      background-size: cover;
      max-height: 516px;
    }

    .container {
      margin: 0 auto 73px;
      max-width: 90%;

      main {
        margin: 0 auto;

      }
    }
  }

}

@include MobileOnly {
  .PCTabletDisplay {
    display: none;
  }

  .mobileDisplay {
    display: block;
  }

  .app {

    .container {
      margin: 0 auto 20px;
      }
    }
  }
