@import '../../app/app';

.modalBox{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  outline: none;
  color:#fff;

  .ladingImg1{
    margin-bottom: 10px;
    color:#fff;
    width: 160px;
    animation: upDown 0.5s infinite alternate;
  }
}

@keyframes upDown {
  from{
    transform: translateY(0);
  }
  to{
    transform: translateY(20px);
  }
}