@import "src/app/app";

.bannerWrap {
  width: 100%;
  max-width: 533px;
  padding: 220px 0 64px 0;
  position: relative;
  text-align: center;
  z-index: 1;
  margin: 0 auto;

  .logo {
    width: 100%;
    height: 100%;
    min-height: 280px;
    background: url("../../assets/images/logo.svg") no-repeat left center;
    background-size: cover;
  }

  .content {
    width: 100%;
    position: relative;
    z-index: 1;

    .period {
      margin: 20px auto;
      padding: 12px 40px;
      background: rgba(0, 4, 29, 0.2);
      border-radius: 100px;
      color: #fff;
      max-width: 340px;
      max-height: 48px;
      font-size: 20px;
      font-weight: 400;

      display: block;
      order: 1;
    }

    p {
      color: #fff;
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
    }

    .point {
      color: #60FBF6;
      font-weight: 700;
      font-size: 20px;
      line-height: 32px;
      margin-bottom: 8px;

    }

  }
}

@include TabletMobile {

  .bannerWrap {
    padding: 60px 0 40px 0;
    margin: 0 auto;

    .logo {
      width: 100%;
      height: 100%;
      max-width: 298px;
      min-height: 170px;
      margin: 0 auto;
      background: url("../../assets/images/m_logo.svg") no-repeat center;
    }

    .content {

      .period {
        max-width: 298px;
        font-size: 14px;
        line-height: 17px;
        padding: 12px 0;
        margin: 10px auto 30px;
      }

      p {
        font-size: 14px;
        line-height: 24px;
      }
    }
  }
}

@include MobileOnly {
  .bannerWrap {
    .logo {
      margin-bottom: 0;
    }
  }
}