@import '../../app/app';


article {
  width: 292px;
  height: 560px;
  background: url("../../assets/images/event1/frame.png") no-repeat center center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;

  .contentBox {
    width: 248px;
    height: 440px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    background: rgba(77, 79, 219, 0.05);
    border-radius: 60px;
    top: 60px;

    .TimeNotice {
      margin-top: 30px;
      font-size: 20px;
      font-weight: 900;
      line-height: 27px;
      font-family: $vitro;
      color: #fff;
    }

    .itemImg {
      position: relative;
      margin-top: -30px;

      img {
        width: 100%;
        height: 100%;
        max-width: 200px;
        max-height: 200px;
        margin: 0 auto 24px;
        z-index: -1;
      }

      .lines {
        width: 100%;
        max-width: 170px;
        height: 10px;
        background: url('../../assets/images/event1/line.png') no-repeat center center;
        margin: 0 auto;
      }
    }


    span {
      display: block;
      font-weight: 400;
      font-size: 14px;
      color: #fff;
      line-height: 23px;
    }

    p {
      font-family: 'Noto Sans KR', sans-serif;
      font-size: 18px;
      font-weight: 700;
      color: #60FBF6;
      line-height: 26px;
      margin-bottom: 30px;
    }
  }

  button {
    overflow: visible;
    box-shadow: none;
    width: 166px;
    height: 46px;
    background: none;
    border-radius: 100px;
    border: 1px solid #A8236D;
    color: #fff;
    font-weight: 400;
    font-size: 20px;
    line-height: 35px;
    top: 70px;
    position: relative;
    z-index: 2;

    &:before {
      content: "";
      width: 100%;
      height: 100%;
      top: -9px;
      left: 0;
      background: url('../../assets/images/event1/button.svg') no-repeat 50% top;
      position: absolute;
      z-index: -1;
    }

    &:after {
      content: "";
      width: 100%;
      height: 100%;
      bottom: -9px;
      left: 0;
      background: url('../../assets/images/event1/button.svg') no-repeat 50% top;
      transform: rotate(-180deg);
      position: absolute;
      z-index: -1;
    }

    &:focus {
      outline: none;
    }
  }
}


.NotLogin {
  .contentBox {
    filter: grayscale(1);
  }

  button {
    border-color: $color-inactive;
    font-size: 20px;
    cursor: pointer;

    &:before {
      filter: grayscale(1);
    }

    &:after {
      filter: grayscale(1);
    }
  }
}


.inActive {
  filter: grayscale(1);
  pointer-events: none;

  .contentBox {
    filter: grayscale(1);
  }

  button {
    border-color: $color-inactive;

    &:before {
      filter: grayscale(1);
    }

    &:after {
      filter: grayscale(1);
    }

  }
}

.getActive {
  .itemImg {
    img {
      filter: grayscale(1);
      opacity: 0.3;
    }

    &:before {
      content: "";
      width: 100%;
      height: 100%;
      background: url('../../assets/images/event1/check.png') no-repeat center center;
      position: absolute;
      left: 0;
      top: -20px;
      z-index: 1;
    }
  }

  button {
    background-color: $color-get;
  }
}

.Active {
  .contentBox {
    box-sizing: border-box;
    border: 1px solid #A8236D;
  }

  button {
    cursor: pointer;

    &:before {
      filter: none
    }

    &:after {
      filter: none
    }
  }
}

@include TabletMobile {
  article {
    width: 100%;
    max-width: 320px;
    max-height: 454px;
    background: url('../../assets/images/event1/m_frame.png') no-repeat center center;
    background-size: contain;
    margin: 25px 0;
    display: flex;

    .contentBox {
      width: 100%;
      height: 100%;
      max-width: 230px;
      max-height: 310px;
      border-radius: 30px;
      top: calc(50% - 310px / 2);

      .TimeNotice {
        font-size: 18px;
        line-height: 24px;
      }

      .itemImg {
        img {
          width: 100%;
          height: 100%;
          max-width: 120px;
          max-height: 120px;
          margin: 20px auto;
        }

        .lines {
          width: 100%;
          max-width: 170px;
          height: 10px;
          background: url('../../assets/images/event1/line.png') no-repeat center center;
          margin: 0 auto 20px;
        }
      }
    }

    button {
      width: 155px;
      margin-top: 50px;
    }

  }
}