@import '../app/app';

.eventTitle {

  .eventBanner {
    width: 100%;
    max-width: 217px;
    height: 40px;
    background: url("../assets/images/EventBanner.png") no-repeat;
    background-size: contain;
    color: #fff;
    font-weight: 700;
    font-size: 20px;
    line-height: 40px;
    margin: 0 auto 32px;
  }

  .eventTopText {
    font-size: 22px;
    font-weight: 500;
    line-height: 32px;
    color: #fff;
  }

  h2 {
    font-family: $vitro;
    text-transform: uppercase;
    margin-top: 20px;
    font-size: 48px;
    font-weight: 900;
    letter-spacing: 1.5px;
    line-height: 72px;
    color: #fff;

    span {
      font-family: $vitro;
      color: #FE4879;
    }
  }

  .eventBottomText {
    font-size: 22px;
    font-weight: 400;
    line-height: 32px;
    margin-top: 10px;
    color: #fff;

    span {
      color: #FE4879;
      font-weight: 700;
    }
  }
}

@include TabletMobile {
  .eventTitle {
    .eventBanner {
      margin: 0 auto 25px;
    }
    h2{
      font-size:2.5em;
      line-height: 1.5em;
      letter-spacing: 0;
    }

    .eventTopText {
      font-size: 1.5em;
      font-weight: 400;
    }
  }
}

@include MobileOnly {
  .eventTitle {
    .eventTopText {
      font-size: 14px;
      line-height: 24px;
    }
    h2{
      font-size:28px;
      line-height: 37px;
      letter-spacing: 0;
    }

    .eventBottomText {
      font-size: 14px;
      line-height: 24px;
      font-weight: 400;
    }
  }
}