@import '../../app/app';

.WeekList {
  width: 100%;
  height: 100%;
  max-height: 100%;
  margin: 60px 8px 0;
  background: none;

  h4 {
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    color: #fff;
    padding: 7px 24px 8px;
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 100px;
    margin-bottom: 17px;
  }

  img {
    width: 100%;
    max-width: 960px;
  }

  .WeekDay {
    color: #FE4879;
    font-size: 32px;
    font-weight: 400;
    line-height: 38px;
    margin-bottom: 25px;
  }

}

@include TabletMobile {
  .WeekList {

    margin: 50px 0 0;
    background: none;



    .WeekDay {
      color: #FE4879;
      font-size: 18px;
      font-weight: 700;
      line-height: 24px;
      margin-bottom: 15px;
    }

    img {
      width: 100%;
      min-width: 100%;
      height: 100%;
    }

  }

}