@import '../../app/app';

.PrecautionsWrap {
  width: 100%;
  height: 100%;
  background: url('../../assets/images/bg_bottom.png') no-repeat bottom right;
  background-size: cover;
  overflow-x: hidden;
  position: relative;

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 8%;
    top: -30px;
    background: #00041D;
    filter: blur(20px);
  }

  @media (min-width:1940px) {
    &:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 10%;
      top: -40px;
      background: #00041D;
      filter: blur(20px);

    }
  }

  .Precautions {
    height: 100%;
    max-width: $content-size;
    //max-width:1024px
    display: flex;
    flex-direction: column;
    padding: 50px 0;
    margin: 0 auto;

    div {
      color: #CCCCCC;
      line-height: 20px;
      font-weight: 400;
      font-size: 14px;
    }

    h3 {
      color: #CCCCCC;
      margin-bottom: 40px;
      font-weight: 500;
      font-size: 32px;
      line-height: 32px;
    }


    h4 {
      color: #CCCCCC;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      margin-bottom: 10px;
    }

    ul {
      margin-bottom: 40px;
      margin-left: 20px;

      li {
        color: #CCCCCC;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        list-style-type: "- ";
        text-align: left;

        span {
          display: block;
          color: #CCCCCC;
        }
      }
    }
  }
}

@include TabletMobile {

  .PrecautionsWrap {

      width: 100%;
      height: 100%;
      background: url('../../assets/images/bg_bottom.png') no-repeat bottom right 45%;
      background-size:400%;

      .Precautions {
      padding: 70px 20px 50px;
      h3{
        font-size: 20px;
      }
      h4 {
        margin-bottom: 40px;
      }

      div {
        margin-left: 20px;
      }

      ul {

        li {
          color: #CCCCCC;
          font-weight: 400;
          font-size: 14px;
          line-height: 20px;
          list-style-type: "- ";
          text-align: left;

          span {
            display: block;
            color: #CCCCCC;
          }
        }
      }
    }
  }

}